  list-style: none;
  display: flex;
  flex-direction: column;
}
@media (max-width: 767px) {
  .main-nav--closed .site-list {
    display: none;
  }
  .main-nav--closed .user-list {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .site-list {
    display: block;
  }
  .main-nav--nojs .user-list {
    display: block;
  }
}
